<template>
  <div>
    <input type="text" v-model="msg" @keyup.enter="send">
    <button @click.enter="send">send</button>
    <div>
      <ul>
        <li v-for="item in list">
          {{item.time}}=>{{item.msg}}
        </li>
      </ul>
    </div>
    <h3 v-if="closed">连接已关闭</h3>
  </div>
</template>
<script>
import formatTime from '@/filters/formatTime'
export default {
  data() {
    return {
      ws: null,
      msg: null,
      list: [],
      closed: false
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.ws = this.createWS(`ws://lp-ws.herokuapp.com/${this.$route.params.id}`)
      // this.ws = this.createWS('ws://localhost:3000/1')
      this.ws.onmessage = msg => {
        this.list.push({
          type: 1,
          msg: msg.data,
          time: formatTime(new Date())
        })
      }
      this.ws.onclose = () => {
        this.closed = true
      }
    },
    createWS(urlValue) {
      if (window.WebSocket) return new WebSocket(urlValue)
      if (window.MozWebSocket) return new MozWebSocket(urlValue)
      return false
    },
    send() {
      this.list.push({
        type: 0,
        msg: this.msg,
        time: formatTime(new Date())
      })
      this.ws.send(this.msg)
      this.msg = null
    }
  }
}
</script>



